@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Roboto);
ul {
    list-style-type: none;
    overflow: hidden;
}

li {
    float: right;
}

li a {
    color: #000;
    display: block;
    text-align: center;
    padding: 10px 10px;
    text-decoration: none;
    font-size: 14px;
}

li a:hover {
    text-decoration: underline;
}

.grid {
    height: 23px;
    position: relative;
    bottom: 4px;
}

.logo {
    margin-top: 200px;
    margin-bottom: 20px;
}

.bar {
    margin: 0 auto;
    width: 625px;
    border-radius: 30px;
    border: 1px solid #dcdcdc;
}

.bar:hover {
    box-shadow: 1px 1px 8px 1px #dcdcdc;
}

.bar:focus-within {
    box-shadow: 1px 1px 8px 1px #dcdcdc;
    outline: none;
}

#searchbar {
    height: 45px;
    border: none;
    width: 500px;
    font-size: 16px;
    outline: none;
    background-color: #f8f8f8;
}

.voice,
.upload {
    height: 30px;
    position: relative;
    top: 8px;
    left: 5px;
    cursor: pointer;
}


.buttons {
    margin-top: 30px;
}

.button {
    background-color: #f5f5f5;
    border: none;
    color: #707070;
    font-size: 15px;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 4px;
    outline: none;
}

.button:hover {
    border: 1px solid #c8c8c8;
    padding: 9px 19px;
    color: #808080;
}

.button:focus {
    border: 1px solid #4885ed;
    padding: 9px 19px;
}

.recording-circle {
    position: relative;
    display: inline-block;
    background-color: red;
    width: 20px;
    height: 20px;
    top: 3px;
    left: 5px;
    border-radius: 50%;
    -webkit-animation: ease pulse 2s infinite;
            animation: ease pulse 2s infinite;
    margin-left: 5px;
    margin-right: 5px;
}

@-webkit-keyframes pulse {
    0% {
        background-color: red;
    }

    50% {
        background-color: #fee6e6;
    }

    100% {
        background-color: red;
    }
}

@keyframes pulse {
    0% {
        background-color: red;
    }

    50% {
        background-color: #fee6e6;
    }

    100% {
        background-color: red;
    }
}
html,
body,
* {
	box-sizing: border-box;
	font-size: 16px;
}

html,
body {
	height: 95%;
	text-align: center;
}

body {
	padding: 2rem;
	background: #f8f8f8;
}

h2 {
	font-family: "Roboto", sans-serif;
	font-size: 26px;
	line-height: 1;
	color: #454cad;
	margin-bottom: 5;
}

p {
	font-family: "Roboto", sans-serif;
	font-size: 18px;
	color: #5f6982;
}

.uploader {
	display: block;
	clear: both;
	margin: 0 auto;
	width: 100%;
	max-width: 600px;
}

.uploader label {
	float: left;
	clear: both;
	width: 100%;
	padding: 4rem 1.5rem;
	text-align: center;
	background: #fff;
	border-radius: 7px;
	border: 3px solid #eee;
	transition: all 0.2s ease;
	-webkit-user-select: none;
	        user-select: none;
}

.uploader label:hover {
	border-color: #454cad;
}

.uploader label.hover {
	border: 3px solid #454cad;
	box-shadow: inset 0 0 0 6px #eee;
}

.uploader label.hover #start i.fa {
	-webkit-transform: scale(0.8);
	        transform: scale(0.8);
	opacity: 0.3;
}

.uploader #start {
	float: left;
	clear: both;
	width: 100%;
}

.uploader #start i.fa {
	font-size: 50px;
	margin-bottom: 1rem;
	transition: all 0.2s ease-in-out;
}

.uploader #response {
	float: left;
	clear: both;
	width: 100%;
}

.uploader #response #messages {
	margin-bottom: 0.5rem;
}

.uploader #file-image {
	display: inline;
	margin: 0 auto 0.5rem auto;
	width: auto;
	height: auto;
	max-width: 180px;
}

.uploader #notimage {
	display: block;
	float: left;
	clear: both;
	width: 100%;
}

.uploader progress,
.uploader .progress {
	display: inline;
	clear: both;
	margin: 0 auto;
	width: 100%;
	max-width: 180px;
	height: 8px;
	border: 0;
	border-radius: 4px;
	background-color: #eee;
	overflow: hidden;
}

.uploader .progress[value]::-webkit-progress-bar {
	border-radius: 4px;
	background-color: #eee;
}

.uploader .progress[value]::-webkit-progress-value {
	background: linear-gradient(to right, #393f90 0%, #454cad 50%);
	border-radius: 4px;
}

.uploader .progress[value]::-moz-progress-bar {
	background: linear-gradient(to right, #393f90 0%, #454cad 50%);
	border-radius: 4px;
}

.uploader input[type="file"] {
	display: none;
}

.uploader div {
	margin: 0 0 0.5rem 0;
	color: #5f6982;
}

.uploader .btn {
	display: inline-block;
	margin: 0.5rem 0.5rem 1rem 0.5rem;
	clear: both;
	font-family: inherit;
	font-weight: 700;
	font-size: 14px;
	text-decoration: none;
	text-transform: initial;
	border: none;
	border-radius: 0.2rem;
	outline: none;
	padding: 0 1rem;
	height: 36px;
	line-height: 36px;
	color: #fff;
	transition: all 0.2s ease-in-out;
	box-sizing: border-box;
	background: #454cad;
	border-color: #454cad;
	cursor: pointer;
}

.tag-input, .tag-input:focus {
	display: inline-block;
	margin: 0.5rem 0.5rem 1rem 0.5rem;
	border-color: #454cad;
	font-family: inherit;
	font-size: 14px;
	font-weight: 700;
	border:1px solid #454cad;
	height: 36px;
	line-height: 36px;
	width: 450px;
}

#upload-response {
	color: red;
	font-weight: bold;
}
